body {
    background-repeat: repeat;
    background-size: auto;
    background-color: rgb(255, 255, 255)
}


.Project {
    border: 3px solid;
    display: flex;
    text-decoration: none;
    padding: 20px 50px;
    border: rgb(240, 145, 2);
    border-radius: 10px;
    flex-flow: row wrap;
    margin: 1rem;
    display: inline-block;
}

.Project img {
    width: 250px;
}

.Project:hover {
    animation: pulsate 1s ease-in-out;
}

@keyframes pulsate {
    0% {
        box-shadow:
            0 0 25px rgb(138, 153, 170),
            0 0 25px rgb(69, 78, 85);
    }
}

/* resume page */
.portfolioBtn {
    text-decoration: none;
    text-transform: uppercase;
    letter-spacing: 2px;
    color: black;
    outline: 2px solid black;
    padding: 30px 60px;
    position: absolute;
    overflow: hidden;
    margin: 0;
    transform: translateY(-50%) translateX(240%);
}

.contactContainer {}

.footer {
    text-align: center;
}

